/* Spring - Software, App, Saas & Product Showcase Landing HTML5 Template */

@media only screen and (max-width: 1700px){
	.page-wrapper{
		overflow:hidden;	
	}
}

@media only screen and (max-width: 1340px){
	.page-wrapper{
		overflow:hidden;	
	}
	
	.main-header .main-box{
		padding:0px;
	}
}

@media only screen and (max-width: 1140px){
	
