@media only screen and (max-width: 767px){
	
	.main-header{
		position:fixed;
		background:#ffffff;	
	}
	
	.main-header.header-style-two{
		position:fixed;
		background:#3b566e;	
	}
	
	.main-header .main-box,
	.main-header.fixed-header .main-box{
		position:relative;
		top:0px;
		padding:0px;
		background:none !important;	
	}
	
	.main-header .main-box:after,
	.main-header.fixed-header .main-box:after{
		opacity:1;	
	}
	
	.page-title{
		padding:190px 0px 100px;	
	}
	
	.page-title h1{
		font-size:32px;	
	}
	
	.main-header .nav-outer{
		width:100%;
		z-index:12;
	}
	
	.main-header .main-menu{
		padding-top:0px;
		width:100%;
		margin:0px;
	}
	
	.main-menu .collapse {
	   max-height:300px;
		overflow:auto;
		float:none;
		width:100%;
		padding:10px 0px 0px;
		border:none;
		margin:0px;
		-ms-border-radius:0px;
		-moz-border-radius:0px;
		-webkit-border-radius:0px;
		-o-border-radius:0px;
		border-radius:0px;
   }
   
	.main-menu .collapse.in,
	.main-menu .collapsing{
		padding:0px 0px 0px;
		border:none;
		overflow:auto;
		margin:0px 0px 15px;
		-ms-border-radius:0px;
		-moz-border-radius:0px;
		-webkit-border-radius:0px;
		-o-border-radius:0px;
		border-radius:0px;	
	}
	
	
	.main-menu .navbar-header{
		position:relative;
		float:none;
		display:block;
		text-align:right;
		padding:10px 0px 15px;
		right:0px;
		z-index:12;
		margin-top:-65px;	
	}
	
	.main-menu .navbar-header .navbar-toggle{
		display:inline-block;
		z-index:7;
		border:1px solid #1f8ceb;
		float:none;
		margin:0px 0px 0px 0px;
		border-radius:0px;
		background:#1f8ceb;
	}
	
	.main-menu .navbar-header .navbar-toggle .icon-bar{
		background:#ffffff;	
	}
	
	.main-menu .navbar-collapse > .navigation{
		float:none !important;
		margin:0px !important;
		width:100% !important;
		border-radius:0px;
		background:#3b566e;
		border:1px solid #ffffff;
		border-top:none;
	}
	
	.main-menu .navbar-collapse > .navigation > li{
		margin:0px !important;
		float:none !important;
		width:100%;
	}
	
	.main-menu .navigation > li > a,
	.main-menu .navigation > li > ul:before{
		border:none;	
	}
	
	.main-menu .navbar-collapse > .navigation > li > a{
		padding:10px 10px !important;
		border:none !important;
	}
	
	.main-menu .navigation li.dropdown > a:after,
	.main-menu .navigation > li.dropdown > a:before,
	.main-menu .navigation > li > ul > li > a::before,
	.main-menu .navigation > li > a::before,
	.main-menu .navigation > li > ul > li > a::before,
	.main-menu .navigation > li > ul > li > ul > li > a::before{
		display:none !important;
	}
	
	.main-menu .navbar-collapse > .navigation > li > ul,
	.main-menu .navbar-collapse > .navigation > li > ul > li > ul{
		position:relative;
		border:none;
		float:none;
		visibility:visible;
		opacity:1;
		display:none;
		margin:0px;
		left:auto !important;
		right:auto !important;
		top:auto !important;
		width:100%;
		background:#3b566e;
		-webkit-border-radius:0px;
		-ms-border-radius:0px;
		-o-border-radius:0px;
		-moz-border-radius:0px;
		border-radius:0px;
		-webkit-transform: translateY(0px);
		-ms-transform: translateY(0px);
		transform: translateY(0px);
		transition:all 500ms ease;
		transition:none !important;
		-webkit-transition:none !important;
		-ms-transition:none !important;
		-o-transition:none !important;
		-moz-transition:none !important;
	}
		
	.main-menu .navbar-collapse > .navigation > li > ul,
	.main-menu .navbar-collapse > .navigation > li > ul > li > ul{
		border-top:1px solid rgba(255,255,255,1) !important;	
	}
	
	.main-menu .navbar-collapse > .navigation > li,
	.main-menu .navbar-collapse > .navigation > li > ul > li,
	.main-menu .navbar-collapse > .navigation > li > ul > li > ul > li{
		border-top:1px solid rgba(255,255,255,1) !important;
		opacity:1 !important;
		top:0px !important;
		left:0px !important;
		visibility:visible !important;
	}
	
	.main-menu .navbar-collapse > .navigation > li:first-child{
		border:none;	
	}
	
	.main-menu .navbar-collapse > .navigation > li > a,
	.main-menu .navbar-collapse > .navigation > li > ul > li > a,
	.main-menu .navbar-collapse > .navigation > li > ul > li > ul > li > a{
		padding:10px 10px !important;
		line-height:22px;
		color:#ffffff;
		font-size:16px;
		background:#3b566e;
		text-align:left;
	}
	
	.main-menu .navbar-collapse > .navigation > li > a:hover,
	.main-menu .navbar-collapse > .navigation > li > a:active,
	.main-menu .navbar-collapse > .navigation > li > a:focus{
		background:#3b566e;
	}
	
	.main-menu .navbar-collapse > .navigation > li:hover > a,
	.main-menu .navbar-collapse > .navigation > li > ul > li:hover > a,
	.main-menu .navbar-collapse > .navigation > li > ul > li > ul > li:hover > a,
	.main-menu .navbar-collapse > .navigation > li.current > a,
	.main-menu .navbar-collapse > .navigation > li.current-menu-item > a{
		background:#3b566e;
		color:#ffffff !important;
	}
	
	.main-menu .navbar-collapse > .navigation li.dropdown .dropdown-btn{
		display:block;
	}
	
	.main-menu .navbar-collapse > .navigation li.dropdown:after,
	.main-menu .navigation > li > ul:before{
		display:none !important;	
	}
	
	.comments-area .reply-comment,
	.news-style-one blockquote{
		margin-left:0px;	
	}
	
	.sec-title{
		text-align:center;	
	}
	
	.features-section-two .features-column .inner:before,
	.features-section-two .features-column .inner:after{
		display:none;	
	}
	
	.feature-block-two{
		margin-bottom:50px;	
	}
	
	.features-section-two .features-container{
		padding-top:50px;	
	}
	
	.footer-style-two .footer-upper .copyright{
		padding-top:0px;	
	}
	
	.feature-block-two .inner-box,
	.feature-block-two .inner-box .text,
	.service-style-two .inner-box{
		text-align:center;	
	}
	
	.what-we-offer .text-column h2:before{
		display:none;	
	}
	
	.what-we-offer .text-column h2{
		padding-bottom:0px;	
	}
	
	.what-we-offer .text-column{
		text-align:center;	
	}
	
	.what-we-offer .text-column .inner{
		padding:0px;	
	}
	
	.review-tabs .tab-buttons .tab-btn{
		padding:10px 20px;
		margin:0px 2.5% 20px;
		width:44%;
		float:none;
		display:inline-block;
		border:1px solid #e0e0e0;	
	}
	
	.review-tabs .tab-buttons .active-btn{
		color:#ffffff;
		background:#3b566e;
		border-color:#3b566e;	
	}
	
	.review-tabs .tab-buttons{
		border:none;
	}
	
	.review-tabs .buttons-box{
		margin-bottom:30px;	
	}
	
	.welcome-section h2,
	.price-section .title-box h2,
	.testimonials-carousel-one .text-column .text{
		font-size:24px;
	}
	
	.welcome-section .text{
		font-size:16px;
	}
	
	.review-tabs .tabs-content .tab .image-column,
	.review-tabs .tabs-content .tab .text-column{
		text-align:center;	
	}
}

