/*** 

====================================================================
	Scroll To Top style
====================================================================

***/

.scroll-to-top{
	position:fixed;
	bottom:20px;
	right:20px;
	width:60px;
	height:60px;
	color:#ffffff;
	font-size:20px;
	text-transform:uppercase;
	line-height:60px;
	text-align:center;
	z-index:100;
	cursor:pointer;
	background:#222222;
	display:none;
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;		
}

.scroll-to-top:hover{
	color:#ffffff;
	background:#1f8ceb;
}

/*** 

====================================================================
	Main Header style
====================================================================

***/

.main-header{
	position:absolute;
	left:0px;
	top:0px;
	z-index:99;
	width:100%;
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;
}

.main-header .auto-container{
	position:relative;
	max-width:1800px;	
}

.main-header.alternate .auto-container,
.main-header.header-style-two .auto-container{
	max-width:1200px;	
}

.main-header .main-box{
	position:relative;
	left:0px;
	top:30px;
	width:100%;
	padding:0px 50px;
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;
}

.main-header.header-style-two .main-box{
	top:0px;
	background:rgba(255,255,255,0.30);	
}

.main-header .main-box:after{
	content:'';
	position:absolute;
	left:0px;
	top:100%;
	height:0px;
	width:100%;
	opacity:0;
	border-bottom:1px solid rgba(0,0,0,0.10);
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;
		
}

.main-header.fixed-header .main-box:after,
.main-header.header-style-two .main-box:after{
	opacity:1;
}

.main-header.fixed-header .main-box{
	position:fixed;
	top:0px;
	z-index:999;
	opacity:1;
	visibility:visible;
	background-color:#ffffff;
}

.main-header.header-style-two.fixed-header .main-box{
	top:0px;
	background:rgba(0,0,0,0.90);	
}

.main-header .main-box .outer-container{
	position:relative;
}

.main-header .main-box .logo-box{
	position:relative;
	float:left;
	left:0px;
	padding:15px 0px;
}

.main-header .main-box .logo-box .logo img{
	display:inline-block;
	max-width:100%;
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;	
}

.main-header .nav-outer{
	position:relative;
	float:right;
}

.main-header .nav-outer .other-links{
	position:relative;
	float:left;
	padding-top:20px;
	margin-left:50px;
}

.main-header.header-style-two .nav-outer .other-links{
	margin-left:30px;	
}

.main-header .nav-outer .other-links .btn-style-one{
	padding:7px 40px;	
}

.main-header.alternate .nav-outer .other-links .theme-btn{
	text-transform:capitalize;
	padding:7px 32px;
}

.main-header.header-style-two .nav-outer .other-links .theme-btn{
	position:relative;
	text-transform:capitalize;
	color:#ffffff;
	top:7px;
	line-height:18px;
	font-size:16px;
	padding:2px 0px 0px 25px;
	border-left:1px solid #ffffff;
}

.main-header.header-style-two .nav-outer .other-links .theme-btn:hover{
	text-decoration:underline;	
}

.main-menu{
	position:relative;
	float:left;
	-webkit-transition:all 300ms ease;
	-ms-transition:all 300ms ease;
	-o-transition:all 300ms ease;
	-moz-transition:all 300ms ease;
	transition:all 300ms ease;	
}

.main-menu .navbar-collapse{
	padding:0px;	
}

.main-menu .navigation{
	position:relative;
	font-family: 'Heebo', sans-serif;
}

.main-menu .navigation > li{
	position:relative;
	float:left;
	margin:0px 0px 0px 40px;
}

.main-menu .navigation > li > a{
	position:relative;
	display:block;
	color:#51718c;
	line-height:30px;
	text-transform:capitalize;
	letter-spacing:0.01em;
	opacity:1;
	padding:25px 0px;
	font-size:18px;
	font-weight:400;
	transition:all 500ms ease;
	-moz-transition:all 500ms ease;
	-webkit-transition:all 500ms ease;
	-ms-transition:all 500ms ease;
	-o-transition:all 500ms ease;
}

.header-style-two .main-menu .navigation > li > a{
	color:#ffffff;	
}

.main-menu .navigation > li > a:before{
	content:'';
	position:absolute;
	left:0px;
	bottom:20px;
	width:0px;
	border-bottom:2px solid #1f8ceb;
	transition:all 500ms ease;
	-moz-transition:all 500ms ease;
	-webkit-transition:all 500ms ease;
	-ms-transition:all 500ms ease;
	-o-transition:all 500ms ease;	
}

.header-style-two .main-menu .navigation > li > a:before{
	bottom:auto;
	top:0px;
	border-bottom-color:#ffffff;	
}

.main-menu .navigation > li:hover > a,
.main-menu .navigation > li.current > a,
.main-menu .navigation > li.current-menu-item > a{
	color:#1f8ceb;
	opacity:1;
}

.header-style-two .main-menu .navigation > li:hover > a,
.header-style-two .main-menu .navigation > li.current > a,
.header-style-two .main-menu .navigation > li.current-menu-item > a{
	color:#ffffff;
}

.main-menu .navigation > li:hover > a:before,
.main-menu .navigation > li.current > a:before,
.main-menu .navigation > li.current-menu-item > a:before{
	width:22px;
}

.header-style-two .main-menu .navigation > li:hover > a:before,
.header-style-two .main-menu .navigation > li.current > a:before,
.header-style-two .main-menu .navigation > li.current-menu-item > a:before{
	width:100%;
}

.main-menu .navigation > li > ul{
	position:absolute;
	left:0px;
	top:100%;
	width:220px;
	padding:0px;
	z-index:100;
	display:none;
	background:#ffffff;
	border-top:2px solid #1f8ceb;
	-webkit-transform: translateY(30px);
	-ms-transform: translateY(30px);
	transform: translateY(30px);
	-webkit-box-shadow:2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
	-ms-box-shadow:2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
	-o-box-shadow:2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
	-moz-box-shadow:2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
	box-shadow:2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
}

.main-menu .navigation > li > ul.from-right{
	left:auto;
	right:100%;	
}

.main-menu .navigation > li > ul > li{
	position:relative;
	width:100%;
	border-bottom:1px solid rgba(0,0,0,0.15);
}

.main-menu .navigation > li > ul > li:last-child{
	border-bottom:none;	
}

.main-menu .navigation > li > ul > li > a{
	position:relative;
	display:block;
	padding:12px 20px;
	line-height:22px;
	font-weight:400;
	font-size:15px;
	text-transform:capitalize;
	color:#222222;
	transition:all 500ms ease;
	-moz-transition:all 500ms ease;
	-webkit-transition:all 500ms ease;
	-ms-transition:all 500ms ease;
	-o-transition:all 500ms ease;
}

.main-menu .navigation > li > ul > li:hover > a{
	background:#3b566e;
	color:#ffffff;	
}

.main-menu .navigation > li > ul > li.dropdown > a:after{
	font-family: 'FontAwesome';
