/*** 

====================================================================
	Main Banner style
====================================================================

***/

.main-banner{
	position:relative;
	z-index:1;
}

/*Banner One*/
.banner-one{
	position:relative;
	padding:250px 0px 100px;
	background-position:left top;
	background-repeat:no-repeat;
	background-size:cover;	
}

.banner-one .text-column,
.banner-one .image-column{
	margin-bottom:30px;	
}

.banner-one .text-column .inner{
	position:relative;
	padding-top:100px;	
}

.banner-one .text-column h4{
	font-size:18px;
	color:#1f8ceb;
	text-transform:capitalize;
	margin-bottom:30px;	
}

.banner-one .text-column h2{
	font-size:36px;
	font-weight:300;
	color:#3b566e;
	margin-bottom:15px;	
}

.banner-one .text-column p{
	font-size:18px;
	margin-bottom:50px;	
}

/*Banner Two*/
.banner-two{
	position:relative;
	padding:270px 0px;
	background-position:center top;
	background-repeat:no-repeat;
	background-size:cover;	
}

.banner-two:before{
	content:'';
	position:absolute;
	bottom:-300px;
	left:0px;
	width:100%;
	height:437px;
	/* background:url(../images/background/banner-shape-1.png) left bottom no-repeat;	 */
}

.banner-two .text-column,
.banner-two .image-column{
	margin-bottom:30px;	
}

.banner-two .text-column .inner{
	position:relative;
	padding-top:40px;	
}

.banner-two .text-column h4{
	font-size:18px;
	color:#1f8ceb;
	text-transform:capitalize;
	margin-bottom:20px;	
}

.banner-two .text-column h2{
	font-size:34px;
	font-weight:400;
	color:#2a2f40;
	margin-bottom:15px;	
}

.banner-two .text-column p{
	font-size:18px;
	margin-bottom:40px;	
}

/*Banner Three*/
.banner-three{
	position:relative;
	padding:340px 0px;
