.footer-style-three .links li a{
	position:relative;
	display:inline-block;
	line-height:24px;
	color:#6f8ba4;
	font-weight:300;
	transition:all 0.3s ease;
	-moz-transition:all 0.3s ease;
	-webkit-transition:all 0.3s ease;
	-ms-transition:all 0.3s ease;
	-o-transition:all 0.3s ease;
}

.footer-style-three .links li a:hover{
	color:#1f8ceb;	
}

.footer-style-three .newsletter-widget{
	position:relative;	
}

.footer-style-three .newsletter-widget h3{
	font-size:20px;
	font-weight:500;
	color:#3b566e;	
}

.footer-style-three .newsletter-widget .text{
	position:relative;
	color:#466077;
	font-weight:300;
	margin-bottom:30px;
}

.footer-style-three .newsletter-widget .form-group{
	position:relative;
	margin:0px;
	max-width:470px;
}

.footer-style-three .newsletter-widget .form-group input[type="text"],
.footer-style-three .newsletter-widget .form-group input[type="email"]{
	position:relative;
	line-height:28px;
	padding:12px 50px 12px 20px;
	border:1px solid #1f8ceb;
	background:#e2eff8;
	color:#101010;
	display:block;
	font-size:16px;
	width:100%;
	height:54px;
	border-radius:4px;
