/*** 

====================================================================
	Clients Section
====================================================================

***/

.clients-section{
	position:relative;
	padding:130px 0px 170px;
	background:#f9fcfe;
}

.clients-section:before{
	content:'';
	position:absolute;
	left:0px;
	top:0px;
	width:100%;
	height:100%;
	/* background:url(../images/background/bg-shape-twelve.png) 0px -200px no-repeat;	 */
}

.clients-section:after{
	content:'';
	position:absolute;
	right:0px;
	top:0px;
	width:100%;
	height:100%;
	background:url(../images/bg-shape-thirteen.png) 100% 200px no-repeat;	
}

.clients-section .auto-container{
	max-width:1000px;
	position:relative;
	z-index:1;	
}

.client-block{
	position:relative;
	margin-bottom:30px;
}

.owl-theme .client-block{
	margin:0px;	
}

.client-block .inner-box{
	position:relative;
	display:block;
	padding:45px 35px;
	background:#fcfeff;
	border:1px solid #deeefc;
	border-radius:3px;
}

.client-block .inner-box .image{
	position:relative;
	margin-bottom:30px;
}

.client-block .inner-box .image img{
	display:inline-block;
	max-width:100%;
	width:auto;	
}

.client-block .inner-box h3{
	color:#3b566e;
	font-size:20px;
	font-weight:500;
	margin-bottom:7px;
}

.client-block .inner-box h3 a{
	color:#3b566e;
}

.client-block .inner-box h3 a:hover{
	color:#1f8ceb;
	text-decoration:underline;
}

.client-block .inner-box .text{
	font-size:18px;
	font-weight:300;
}

.clients-carousel .owl-nav{
	display:none;	
}

.clients-carousel .owl-dots{
	position:absolute;
	left:0px;
	bottom:-70px;
	width:100%;
	text-align:center;	
}

.clients-carousel .owl-dots .owl-dot{
	position:relative;
	display:inline-block;
	margin:0px 5px;	
}

.clients-carousel .owl-dots .owl-dot span{
	position:relative;
	display:block;
	width:14px;
	height:14px;
	border:2px solid #1f8ceb;
	border-radius:50%;
	transition:all 500ms ease;
	-moz-transition:all 500ms ease;
	-webkit-transition:all 500ms ease;
	-ms-transition:all 500ms ease;
	-o-transition:all 500ms ease;
	-webkit-transform:scale(0.7);
	-ms-transform:scale(0.7);
	-o-transform:scale(0.7);
	-moz-transform:scale(0.7);
	transform:scale(0.7);		
}

.clients-carousel .owl-dots .owl-dot.active span{
	background:#1f8ceb;
	-webkit-transform:scale(1);	
	-ms-transform:scale(1);	
	-o-transform:scale(1);	
	-moz-transform:scale(1);	
	transform:scale(1);	
}

