/*** 

====================================================================
	Page Title Style
====================================================================

***/

.page-title{
	position:relative;
	padding:210px 0px 120px;
	background-size:cover;
	background-position:center center;
	background-repeat:no-repeat;
	text-align:center;
	overflow:hidden;
	border-bottom:2px solid #f5fafe;
}

.page-title .auto-container{
	position:relative;
	z-index:1;
}	

.page-title h1{
	position:relative;
	font-size:36px;
	font-weight:400;
	color:#2a2f40;
	text-transform:capitalize;
	line-height:1.4em;
	word-spacing:5px;
	margin-bottom:15px;
}

.page-title .page-breadcrumb{
	position:relative;
	padding:8px 25px;
	display:inline-block;
	border:1px solid #f4f9fe;
}

.page-title .page-breadcrumb li{
	position:relative;
	color:#2a2f40;
	font-size:18px;
	font-weight:300;
	padding-right:6px;
	margin-right:14px;
	display:inline-block;
}

.page-title .page-breadcrumb li:after{
	position:absolute;
	content:'/';
	right:-10px;
	top:7px;
	font-size:16px;
	line-height:1em;
}

.page-title .page-breadcrumb li:last-child{
	margin-right:0px;
	padding-right:0px;
}

.page-title .page-breadcrumb li:last-child::after{
	display:none;
}

.page-title .page-breadcrumb li a{
	position:relative;
	color:#2a2f40;
	font-size:16px;
	font-weight:300;
	display:inline-block;
}

.page-title .page-breadcrumb li a:hover{
	color:#1f8ceb;	
}

.page-title .left-curve{
	position:absolute;
	left:0px;
	top:0px;
	width:344px;
	height:296px;
	/* background:url(../images/background/bg-shape-sixteen.png) no-repeat; */
}

.page-title .right-curve{
	position:absolute;
	right:35px;
	top:0px;
	width:785px;
	height:450px;
	/* background:url(../images/background/bg-shape-fifteen.png) no-repeat; */
}

