}

.team-block .inner-box .upper-box .image{
	position:relative;
	width:170px;
	height:170px;
	margin:0 auto;
	border-radius:50%;
	overflow:hidden;
}

.team-block .inner-box .upper-box .image .social-box{
	position:absolute;
	left:0px;
	top:-10%;
	width:100%;
	height:100%;
	display:block;
	opacity:0;
	margin-top:-12px;
	text-align:center;
	-webkit-transition: all 900ms ease;
    -ms-transition: all 900ms ease;
    -o-transition: all 900ms ease;
    transition: all 900ms ease;
}

.team-block .inner-box:hover .upper-box .image .social-box{
	opacity:1;
	top:50%;
}

.team-block .inner-box .upper-box .image .social-box a{
	position:relative;
	color:#ffffff;
	font-size:14px;
	margin:0px 6px;
}

.team-block .inner-box .upper-box .image:before{
	position:absolute;
	content:'';
	left:0px;
	top:0px;
	width:100%;
	height:100%;
	display:block;
	opacity:0;
	background-color:rgba(31,140,235,0.70);
	-webkit-transition: all 900ms ease;
    -ms-transition: all 900ms ease;
    -o-transition: all 900ms ease;
    transition: all 900ms ease;
    -webkit-transform: perspective(400px) rotateY(-90deg);
    -moz-transform: perspective(400px) rotateY(-90deg);
    -ms-transform: perspective(400px) rotateY(-90deg);
    -o-transform: perspective(400px) rotateY(-90deg);
    transform: perspective(400px) rotateY(-90deg);
    -webkit-transform-origin: center;
    -moz-transform-origin: center;
    -ms-transform-origin: center;
    -o-transform-origin: center;
    transform-origin: center;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.team-block .inner-box:hover .upper-box .image:before{
	opacity:1;
	-webkit-transform: perspective(400px) rotateY(0deg);
    -moz-transform: perspective(400px) rotateY(0deg);
    -ms-transform: perspective(400px) rotateY(0deg);
    -o-transform: perspective(400px) rotateY(0deg);
    transform: perspective(400px) rotateY(0deg);
}

